
html {
  font-size: 12px;
}
.generator {
  padding: 30px;
  padding-top: 10px;
}
.container {
  font-family: 'Source Code Pro', 'Monaco','menlo', monospace !important;
  width: 49% !important;
  height: 100% !important;
  margin-top: 55px;
}
.CodeMirror {
  height: 100vh !important;
  margin-top: 40px;
  position: fixed;
  left: 0;
  top: 0;
}
.label-header {
  font-size: 20px;
  margin-bottom: 10px;
}
.remove-button-wrapper {
  text-align: right;
}
.remove-button-wrapper .button {
  font-size: .75em;
}
.label-header.header {
  padding: .5em;
  margin: 0;
}
.mini {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  font-size: 14px;
}
.guide {
  width: 15px;
}
.introduce {
  padding: 5px;
  border-top: 1px;
  position: relative;
  height: 40px;
  clear: both;
  display: block;
  background: #fff;
}
.introduction {
  width: 50%;
  position: absolute;
  left: 25%;
  right: 25%;
  bottom: 0;
  background: #fff;
  padding: .6em;
}
.introduction.intro-active {
  box-shadow: 0px 0px 1px 1px rgba(0,0,0,0.2);
}
.introduction h2 {
  font-size: 1em;
  cursor: pointer;
}
span.hide-intro {
  float: right;
}



